import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import { trackPromise } from 'react-promise-tracker';
import { deleteData, apiKey } from '../../../GeneralComponents/AJAX/HttpRequest';
import Modal from '../../../GeneralComponents/modalComponent/modal.component';
import Comments from '../Comments/Comments.component';
import ModifyArticle from '../../../GeneralComponents/FormComponent/ModifyPostComponent/ModifyArticle';
import ModifyGIF from '../../../GeneralComponents/FormComponent/ModifyPostComponent/ModifyGIF';
import { UsersContext } from '../../../GeneralComponents/context/usersContext';
import '../mainContent.style.css';
class Content extends Component {
  constructor(props) {
    super(props);
    this.state = { 
      isModalOpen: false,
      renderArticleForm: false,
      renderGIF: false
      }
  }

  static contextType = UsersContext;
  closeModal = () => this.setState({isModalOpen: false})
  toggleModal = (event)=> {
      event.preventDefault();
      const target = event.target;
      console.log(target);
      const targetClassList = [...target.classList];
      console.log(targetClassList);
      (targetClassList.includes('close')) ? (
        this.setState({isModalOpen: false })
        ) : (
          this.setState({ isModalOpen: true})
          )
      console.log(this.state.isModalOpen);
  }

  checkIfUserFlagged = (flagStat, userId) => {
    return flagStat.find(arr => userId === arr.userId)
  }

  modifyArticle = (event) => {
    event.preventDefault();
    const target = event.target;
    const targetClassList = [...target.classList];
    console.log(targetClassList);
    (targetClassList.includes('close')) ? (
      this.setState({renderArticleForm: false })
      ) : (
        this.setState({ renderArticleForm: true})
        )
  }

  modifyGIF = (event) => {
    event.preventDefault();
    const target = event.target;
    const targetClassList = [...target.classList];
    console.log(targetClassList);
    (targetClassList.includes('close')) ? (
      this.setState({renderGIF: false })
      ) : (
        this.setState({ renderGIF: true})
        )
  }

  handleDelete = (event) => {
    event.preventDefault();
    const { feed: { type, id }, allFeed, updateFeed, userData: { token, userId } } = this.props;
    const endpoint = type === 'article' ? 'articles' : 'gifs';
    trackPromise(
      deleteData(`${apiKey}/${endpoint}/${id}`, userId, token)
      .then((res) => {
        console.log(res);
        console.log('done')
        const filterFeed = allFeed.filter(feed => feed.id !== id);
        console.log(filterFeed)
        updateFeed(filterFeed)
      }).catch(err => console.log(err))
    )
  }

    
  contentType = (type, id, content, title) => {
    switch(type) {
      case 'article' :
        return (
          <div className="text-content padding-bottom-sm">
            <p className="font-weight-bold font-sm padding-bottom-sm">
              {title}
            </p>
            <p className="font-sm" style={{overflowY: 'hidden', maxHeight: '150px'}}>{content[0]}</p>
            <Link to={`/articles/${id}`}>
              <span className='padding-sm paint-text font-weight-bold'>...Read More</span>
            </Link>
          </div>
        )
      case 'gif' :
        return (
          <div className="padding-bottom-sm">
            <p className="font-weight-bold font-sm padding-bottom-md">
              {title}
            </p>
            <div className="d-flex justify-content--center align-items--center padding-bottom-md">
              <div className="" style={{width: '70%', height: '150px', overflow: 'hidden'}}>
                <img className="" style={{width: '100%', height: 'auto'}}
                  src={content[0]} publicId = {content[1]}
                  alt="poster avatar"
                />
              </div>
            </div>
            <Link to={`/gifs/${id}`}>
              <span className='padding-md paint-text font-weight-bold'>...View full size</span>
            </Link>
          </div>
        )
        // break;
      default:
        break;
    }
  }

  render() {
    let { users } = this.context;
    users = users.map(user => {
      const { employeeId, photoDetails, firstName, lastName } = user;
      return { employeeId, photoDetails, firstName, lastName }
    });
    const { id, title, category, content, userid, type, flagStat, comments } = this.props.feed;
    const author = users.find(user => user.employeeId === userid);
    console.log(author);
    const { photoDetails, firstName, lastName, employeeId: authorId } = author;
    let { createdon: createdOn } = this.props.feed;
    createdOn = new Date(createdOn).toUTCString();
    const { userId, token } = this.props.userData;
    console.log(userId);

    return ( 
      <article className="story margin-bottom-md">
        {authorId === parseInt(userId, 10) ? (
          <div className="d-flex" style={{justifyContent: 'flex-end'}}>
            <div className="padding-bottom-sm trigger-more-action cursor-pointer position-relative">
              <i className="fas fa-ellipsis-h font-md font-weight-bold color-dark tooltip"></i>
              <div className="more-option padding-md border-r-10">
                <div className="d-flex align-items-end font-weight-bold margin-bottom-sm padding-sm padding-bottom-xsm"
                  onClick={(event) => this.handleDelete(event)}>
                  <i className="fas fa-trash font-sm margin-right-sm"></i>
                  <span className="font-sm">Delete {type === 'article' ? 'article' : 'GIF'}</span>
                </div>
                <div className="d-flex align-items-end font-weight-bold padding-sm padding-bottom-xsm"
                  onClick={(event) => type === 'article' ?  this.modifyArticle(event) : this.modifyGIF(event)}>
                  <i className="fas fa-pen font-sm margin-right-sm"></i>
                  <span className="font-sm">Edit {type === 'article' ? 'article' : 'GIF'}</span>
                </div>
              </div>
            </div>
          </div>
        ) : null}
        <div className="margin-bottom-sm">
          <div className="d-flex justify-content--s-between margin-bottom-sm">
            <div className="poster--details">
              <Link to={authorId === parseInt(userId, 10) ? '/user/profile' : `/user/${firstName}_${lastName}`}
                style={{color: '#000'}}>
                <div className="d-flex nowrap">
                  <img className="avatar--sm icon" 
                  src={photoDetails[0]}
                    alt="poster avatar" />
                  <div className="d-flex column">
                    <span className="poster--username text-content font-weight-bold margin-bottom-xsm">{`${firstName} ${lastName}`}</span>
                    <div className="margin-bottom-sm">
                      <span className="d-block">{'2 minutes ago'}</span>
                    </div>
                  </div>
                </div>
              </Link>
            </div>
            <div className="flag-stats justify-self-end">
              <i className={`fas fa-flag margin-right-sm cursor-pointer ${this.checkIfUserFlagged(flagStat, userId) ? 'danger-text' : ''}`}></i>
              <span className="flag font-weight-bold font-md padding-sm border-r-circle">{flagStat.length === 0 ? '' : flagStat.length}</span>
            </div>
          </div>
        </div>
        <div className="post-content margin-bottom-sm">
          {this.contentType(type, id, content, title)}
        </div>
        <div className="category">
          {category.map((el, index) => {
              return <span key={index} className="border-r-10 font-xsm font-weight-500 bckgr-color2 color-white padding-sm margin-right-sm margin-bottom-sm">{el}</span>
          })}
        </div>
        <div className="comment--flag--section padding-top-sm">
          <div className="stats d-flex justify-content--s-between align-items--center margin-bottom-sm
            padding-bottom-sm">
            <div className="comment-stats font-weight-bold cursor-pointer">
              <i className="fas fa-comments margin-right-sm paint-text"></i>
              <span className="value comment icon">{comments.length}</span> 
            </div>
            <div className="action padding-sm" id="add--comment"
              style={{border: '1px solid #a0a0a0', borderRadius: '10px', justifySelf: 'flex-end'}}
              onClick={(event) => this.toggleModal(event)}
              >
              <i className="fas fa-comment icon color2"></i>
              <span className="font-weight-bold">Comment</span>
            </div>
          </div>
        </div>
        <Modal toggleModal = {this.toggleModal} isModalOpen = {this.state.isModalOpen} contentTitle ="View Comments"
          contentBody = {<Comments comments={comments} feedId={id} users={users} feedType={type} />}
        />
        <Modal toggleModal = {this.modifyArticle} isModalOpen = {this.state.renderArticleForm} contentTitle ="Modify Article"
          contentBody = {<ModifyArticle title={title} article = {content[0]} category={category} articleId={id}
          userData={{userId, token}} closeModal={this.closeModal} />}
        />
        <Modal toggleModal = {this.modifyGIF} isModalOpen = {this.state.renderGIF} contentTitle ="Modify GIF"
          contentBody = {<ModifyGIF title={title} publicUrl = {content[0]} category={category} gifId={id}
          userData={{userId, token}}  closeModal={this.closeModal} />}
        />

      </article>
    );
  }
}

export default Content;